import React from 'react';
import { Skills } from './MySkills.css';
import svgSkills from '../img/svgSkills.svg'
const icons = [
   {
      color: '#f34f29',
      class: 'fab fa-git-alt',
      description: 'Git'
   },
   {
      color: '#61dbfb',
      class: 'fab fa-react',
      description: 'React'
   },
   {
      color: '#f0db4f',
      class: 'fab fa-js-square',
      description: 'JavaScript'
   },
   {
      color: '#F16529',
      class: 'fab fa-html5',
      description: 'HTML'
   },
   {
      color: '#264de4',
      class: 'fab fa-css3-alt',
      description: 'CSS'
   },
   {
      color: '#cc6699',
      class: 'fab fa-sass',
      description: 'SASS'
   },
]
const MySkills = () => {
   return (
      <Skills>
         <header>
            <h1>My Skills</h1>
         </header>
         <section>
            <div>
               {icons.map(icon => {
                  return (
                     <div>
                        <i style={{ color: icon.color }} class={icon.class}></i>
                        <p>{icon.description}</p>
                     </div>
                  )
               })}
            </div>
            <img src={svgSkills} alt="photo" />
         </section>

      </Skills >

   );
}

export default MySkills;